

















import Vue from 'vue';

export default Vue.extend({
  name: 'LanguageSwitcher',
  computed: {
    current(): string {
      return this.$i18n.locale;
    }
  },
  methods: {
    switchLang(lang: string) {
      if (this.$i18n.locale !== lang) {
        this.$i18n.locale = lang;
      }
    }
  }
});
